import './tarifs.css'
import pr from '../../assets/Photos/pr-2.jpg'

export default function Tarifs() {
    return (
        <div className='tarifs-ctn'>
            <img 
                src={pr} 
                alt='Un troupeau de chevaux blancs dans un pr'
                className='tarifs-img'
            />
            <div className='tarifs-flex'>
                <div className='tarifs'>
                    <h2>Tarifs</h2>
                    <p>
                        <div>Sance individuelle 1h : 60 .</div> 
                        <div>Sance de groupe (max 4 personnes) 1h30 : 150  pour le groupe. </div>
                        <br/>
                        Nous sommes deux praticiennes pour les sances de groupes.<br/>
                        Vous avez la possibilit de me joindre pour commander des bons cadeaux.
                    </p>
                </div>
                <div className='aides'>
                    <h2>Aides</h2>
                    <p>
                        Les sances dquithrapie font partie dune psychothrapie assiste par le cheval,<br/> 
                        et sont donc partiellement prises en charge par certaines mutuelles.<br/>
                        <br/>
                        Par ailleurs, toute personne souffrant dun handicap reconnu auprs de la MDPH <br/>
                        (Maison dpartementale des personnes en situation de handicap), <br/>
                        peut tre ligible  une prise en charge totale des cots des sances. 
                    </p>
                </div>
            </div>
        </div>
    )
}