import pr from '../../assets/Photos/pr.jpg'
import './error.css'
import { Link } from 'react-router-dom'

export default function Error() {
    return (
        <div className='flex-center'>
            <div className='error-img-ctn'>
                <img 
                    src={pr} 
                    className='error-img' 
                    alt='Des chevaux dans un pr devant un couch de soleil'
                />
                <div className='error-txt-ctn'>
                    <h2 className='error-txt-abs'>ERROR 404</h2>
                    <h3 className='error-txt-abs'>Oups, la page que vous recherchez n'existe pas</h3>
                    <Link to='/' className='error-txt-abs'>Revenir  la page d'accueil</Link>
                </div>
            </div>
        </div>
    )
}